<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import queryString from "querystring"
import page from 'data/page'
import { DateDay, ScondDay } from "utils/formData"
import { getSchool } from "data/listBody";

const dataOption = {
  leftMenu: [
    // { id:'1', name:'服务名称：', placeholder:'请输入服务名称', module:'id', type:'select'},
    { id:'2', name:'校区：', placeholder:'请输入校区', module:'schoolId', type:'select'},
  ],
  rightMenu: [
    { id:'r1', name:'查询'},
    { id:'r2', name:'取消'},
  ]
}

const dataTable = {
  table: [
    { label:'校区', prop:'schoolName'},      
    { label:'班级', prop:'className'},
    { label:'带班老师', prop:'teacherName'},
    { label:'参加人数', prop:'joinCount'},
    { label:'总人数', prop:'totalCount'},
    // { label:'结束时间', prop:'contactPerson'},
    // { label:'家长会', prop:'mobile'},
    // { label:'电话沟通', prop:'mobile'},
    // { label:'公开课', prop:'mobile'},
  ],
  edit: [
    // {name: '查看', id: 't1'},
  ]
}

const tips = ['确认删除该条信息?'];

export default {
  name: 'serviceList',
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      currentPage: 1,
      id: ''
    }
  },
  components: {
    Options,
    Tables
  },  
  mounted() {
    this.init();
    this.dropInit();
  },
  methods:{
    init() {
      const { type, id } = this.$route.query;
      this.type = type;
      this.id = id;
      this.search();
    },
    dropInit() {
      getSchool(res => {
        let { leftMenu } = this.dataOption;
        leftMenu = leftMenu.map(item => {
          if (item.module === "schoolId") item.options = res;
          return item;
        });
        this.dataOption.leftMenu = leftMenu;
      });
    },
    optchange(params){
      console.log(params.id + '--id被单击了')
      if(params.id === 'r1') return this.search();
      if(params.id === 'r2') return this.$router.go(-1);
    },
    tabchange(params){
      console.log(params.id + '--id被单击了')

      this.id = params.data.id;
      // 查看
      if(params.id === 't1') return this.$router.push(`/service/detail?type=0&id=${this.id}`);
    },
    sure(){
      this.api.delete(`/server/${this.id}`).then((res)=>{
        this.loading = false;
        this.search();
        this.$message(res.data);     
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
      this.dialogVisible = false;
    },
    search(){  //搜索
      const optParams = this.$refs.goodsOpt.getData();
      const pageParams = { size: 20, page: this.currentPage -1 };
      const params = Object.assign({}, optParams, pageParams);
      const param = queryString.stringify(params || {});
      this.loading = true;
      this.api.get(`/server/${this.id}/classJoin?${param}`).then((res)=>{
        this.loading = false;
        this.dataInit(res.data);
        this.setPage(res.data);
        this.tableList = res.data.list;
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    dataInit(data){//数据初始化
    }
  }
}
</script>
 
<style lang="less" scoped>

</style>
 